///<reference path="../../../../node_modules/rxjs/internal/Observable.d.ts"/>
import {Component, ElementRef, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {MusService} from '../../servises/mus.service';
import {filter, flatMap, map} from 'rxjs/operators';
import {from} from 'rxjs';
import {Constans} from '../../untils/constans';

@Component({
    selector: 'app-player',
    templateUrl: './player.component.html',
    styleUrls: ['./player.component.css']
})
export class PlayerComponent implements OnInit, OnDestroy {
    songs = [];
    prefix;
    audio = new Audio();
    music = [];
    currenPosition = 0;
    isPlay = false;
    images = [];
    bgImage = '';
    time;


    constructor(private mus: MusService) {


    }

    ngOnInit() {

        this.prefix = Constans.url();
        this.mus.getMusic()
            .pipe(map(res => res['music']))
            .subscribe(res => {
                this.songs = res;
                const path = res;
                from(path)
                    .pipe(map(e => this.music.push(e['path'])))
                    .subscribe();
                from(path)
                    .pipe(map(c => this.images.push(c['cover'])))
                    .subscribe();
            });

    }

    pl(m = this.currenPosition) {
        this.isPlay = true;

        this.currenPosition = m;
        this.audio.src = `${this.prefix}${this.music[m]}`;
        this.audio.load();
        this.audio.play();

        this.bgImage = `${this.prefix}${this.images[m]}`;

        console.log(this.audio.duration);


    }


    nxt() {
        if (this.currenPosition >= this.music.length - 1) {
            this.currenPosition = 0;
        } else {
            this.currenPosition = this.currenPosition + 1;
        }
        this.pl();
    }

    pr() {
        if (this.currenPosition === 0) {
            this.currenPosition = this.music.length - 1;
        } else {
            this.currenPosition = this.currenPosition - 1;
        }
        this.pl();
    }

    ps() {
        this.isPlay = false;


        this.audio.pause();
        this.bgImage = '';
    }

    ngOnDestroy(): void {
        this.audio = new Audio();
    }
}
