import {Component, OnInit, ViewChild} from '@angular/core';
import {MusService} from '../../servises/mus.service';
import {Constans} from '../../untils/constans';
import {HttpEventType} from '@angular/common/http';

@Component({
    selector: 'app-music',
    templateUrl: './music.component.html',
    styleUrls: ['./music.component.css']
})
export class MusicComponent implements OnInit {
    musics = [];
    title = '';
    prefix = '';
    loading = '';
    @ViewChild('fileInput') fileInput = null;
    @ViewChild('fileCover') fileCover = null;
    year = '';
    genre = '';
    singer = '';

    constructor(private mus: MusService) {

    }

    ngOnInit() {
        this.getMusic();
    }

    getMusic() {
        this.prefix = Constans.url();
        this.mus.getMusic().subscribe(res => {
            this.musics = res['music'];
            console.log(res);
        });
    }

    addMusic() {
        if (
            !this.title ||
            !this.singer ||
            !this.genre ||
            !this.year ||
            !this.fileInput.nativeElement.files[0] ||
            !this.fileCover.nativeElement.files[0]
    )
        {
            return;
        }
        const file = this.fileInput.nativeElement.files[0];
        const cover = this.fileCover.nativeElement.files[0];
        const formData = new FormData();

        formData.append('title', this.title);
        formData.append('singer', this.singer);
        formData.append('genre', this.genre);
        formData.append('year', this.year);
        formData.append('file', file);
        formData.append('cover', cover);

        this.mus.postMusic(formData)
            .subscribe(events => {

                if (events.type === HttpEventType.UploadProgress) {
                    this.loading = `loading: ${Math.round(events.loaded / events.total * 100)}%`;
                    console.log(events);
                } else {
                    this.loading = 'dowload';
                    setTimeout(() => this.loading = '', 2000);
                    console.log(events);
                }

                this.getMusic();
            });
    }

}
