import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {AppComponent} from './app.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {PostComponent} from './components/post/post.component';
import {PostsComponent} from './components/posts/posts.component';
import {MusicComponent} from './components/music/music.component';
import {AuthGuard, NoAuthGuard} from './servises/auth.guard';
import {Router, RouterModule, Routes} from '@angular/router';
import {AuthComponent} from './auth/auth.component';
import {PagenotfoundComponent} from './pagenotfound/pagenotfound.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { MymusicComponent } from './components/mymusic/mymusic.component';
import { CoverComponent } from './components/cover/cover.component';
import { PlayerComponent } from './components/player/player.component';
import { AudioComponent } from './components/audio/audio.component';

const routes: Routes = [
    {path: '', redirectTo: '/music', pathMatch: 'full'},
    {path: 'music', component: MusicComponent, canActivate: [AuthGuard]},
    {path: 'mymusic', component: MymusicComponent, canActivate: [AuthGuard]},
    {path: 'player', component: PlayerComponent, canActivate: [AuthGuard]},
    {path: 'audio', component: AudioComponent, canActivate: [AuthGuard]},
    {path: 'auth', component: AuthComponent, canActivate: [NoAuthGuard]},
    {path: '**', component: PagenotfoundComponent},
];

@NgModule({
    declarations: [
        AppComponent,
        PostComponent,
        PostsComponent,
        MusicComponent,
        AuthComponent,
        PagenotfoundComponent,
        NavbarComponent,
        MymusicComponent,
        CoverComponent,
        PlayerComponent,
        AudioComponent
    ],
    imports: [
        BrowserModule,
        FormsModule,
        HttpClientModule,
        ReactiveFormsModule,
        RouterModule.forRoot(routes)
    ],
    providers: [AuthGuard, NoAuthGuard],
    bootstrap: [
        AppComponent
    ]
})
export class AppModule {
}
