import {Component, OnInit} from '@angular/core';
import {MusService} from '../../servises/mus.service';
import {Constans} from '../../untils/constans';

@Component({
    selector: 'app-mymusic',
    templateUrl: './mymusic.component.html',
    styleUrls: ['./mymusic.component.css']
})
export class MymusicComponent implements OnInit {
    musics = [];
    prefix;

    constructor(private mus: MusService) {
    }

    ngOnInit() {
        this.prefix = Constans.url();
        this.mus.getMyMusic().subscribe(
            (res) => {
                this.musics = res['music'];
                console.log(res);
            }
        );
    }

}
