import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders, HttpParams, HttpRequest} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {Constans} from '../untils/constans';
import {AuthService} from './auth.service';

@Injectable({
    providedIn: 'root'
})
export class MusService {


    constructor(private http: HttpClient, private auth: AuthService) {
    }

    getMusic(): Observable<any> {
        const headers = new HttpHeaders()
            .set('Authorization', `Bearer ${this.auth.getToken()}`);
        return this.http.get(Constans.url() + 'getMusic', {headers});
    }
    getMyMusic(): Observable<any> {
        const headers = new HttpHeaders()
            .set('Authorization', `Bearer ${this.auth.getToken()}`);
        return this.http.get(Constans.url() + 'getUserMusic', {headers});
    }

    postMusic(formData): Observable<any> {
        const headers = new HttpHeaders()
            .set('Authorization', `Bearer ${this.auth.getToken()}`);
        return this.http.post(Constans.url() + 'apiImg', formData, {
            headers,
            reportProgress: true,
            observe: 'events'
        });

    }

}
