import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {MusService} from '../../servises/mus.service';
import {Constans} from '../../untils/constans';
import {from} from 'rxjs';
import {map} from 'rxjs/operators';

@Component({
    selector: 'app-audio',
    templateUrl: './audio.component.html',
    styleUrls: ['./audio.component.css']
})
export class AudioComponent implements OnInit, AfterViewInit {
    @ViewChild('player') player: ElementRef;
    prefix;
    musicList = [];
    imgList = [];
    allMusic = [];
    cur = 0;


    constructor(private mus: MusService) {
        this.prefix = Constans.url();
    }

    ngOnInit() {
        this.playP();
        this.mus.getMusic()
            .subscribe(res => {
                const arr = res['music'];
                this.allMusic = res['music'];
                from(arr)
                    .pipe(map(e => this.musicList.push(this.prefix + e['path'])))
                    .subscribe();
                from(arr)
                    .pipe(map(c => this.imgList.push(this.prefix + c['cover'])))
                    .subscribe();

            });
    }

    ngAfterViewInit(): void {

    }


    nextP() {

        if (this.cur === this.musicList.length - 1) {
            this.cur = 0;
        } else {
            this.cur++;
        }
        this.playP();


    }

    prevP() {

        if (this.cur === 0) {
            this.cur = this.musicList.length - 1;
        } else {
            this.cur--;
        }
        this.playP();


    }

    playP(count = this.cur) {
        this.cur = count;
        this.player.nativeElement.play();

    }

    pauseP() {
        this.player.nativeElement.pause();
    }
}
