import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable, of} from 'rxjs';
import {Constans} from '../untils/constans';
import {map, tap} from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class AuthService {
    constructor(private http: HttpClient) {
    }

    getToken() {
        const t = localStorage.getItem('token');
        return t ? t : null;
    }

    setToken(token) {
        localStorage.setItem('token', token);
    }

    clearToken() {
        localStorage.removeItem('token');
    }

    public register(email, password): Observable<any> {
        return this.http.post(Constans.url('/api/auth/register'), {
            email: email, password: password
        }).pipe(
            tap((res) => {
                if (res['token']) {
                    this.setToken(res['token']);
                }
            })
        );
    }

    public login(email, password): Observable<any> {
        return this.http.post(Constans.url('/api/auth/login'), {
            email: email, password: password
        }).pipe(
            tap((res) => {
                if (res['token']) {
                    this.setToken(res['token']);
                }
            })
        );
    }

    public logOut() {
        this.clearToken();
    }

    public isAuth() {
        return of(this.getToken() !== null);
    }


}
